import React, {memo, useCallback, useRef} from 'react';
import {
  interpolateColor,
  runOnUI,
  useAnimatedStyle,
  useSharedValue,
} from 'react-native-reanimated';
import {StyleSheet} from 'react-native';
import {
  Colors,
  Fonts,
  getUser,
  getUserByQuery,
  getUserGroup,
  goBack,
  styled,
  useAsyncFn,
  useAutoToastError,
  useNavigationParams,
  useTheme,
} from '@true/core';
import {
  AnimatedBottomSpace,
  AnimatedKeyboardAvoidingViewRef,
  FullScreenWrapper,
  LeftModalHeader,
  MyKeyboardAvoidingView,
  UIBottomButton,
  UIButton,
  UIUserPickerInput,
} from '@true/ui';
import {IC_OUTLINED_RIGHT} from '@true/asset';

export interface AddMemberScreenProps {
  ids: number[];
  onSubmit: (ids: number[], usernameIds: string) => void;
}

export const AddMemberScreen = memo(() => {
  const {ids, onSubmit} = useNavigationParams<AddMemberScreenProps>();
  const offset = useSharedValue(100);
  const ref = useRef<AnimatedKeyboardAvoidingViewRef>(null);
  const scrollRef = useRef<any>(null);
  const inputRef = useRef<any>(null);
  const theme = useTheme();
  const followers = ids;
  const params = useSharedValue({
    followers: (followers && followers) || [],
    groups: [],
  });

  const onChangeValue = useCallback(
    (key: string, value: any) => {
      if (key === 'groups') {
        let _followers = params.value.followers;
        value
          .map((groupId: any) => {
            const group = getUserGroup(groupId);
            if (group && group.metatype === 'group') {
              _followers = _followers.concat(
                // @ts-ignore
                getUserByQuery(`userGroup/${groupId}`),
              );
            }
          })
          .filter(Boolean);
        params.value = {
          ...params.value,
          followers: [...new Set(_followers)],
        };

        return;
      }
      runOnUI((_key, _value) => {
        'worklet';
        params.value = {
          ...params.value,
          // @ts-ignore
          [_key]: _value,
        };
      })(key, value);
    },
    [params],
  );

  const [{loading: shareLoading, error: shareError}, onRequestAdd] =
    useAsyncFn(async () => {
      let _followers: any[] = params.value.followers;

      const _followersUserName = [...new Set(_followers)].map(
        id => '@' + getUser(id)?.username,
      );
      onSubmit(_followers, _followersUserName.join(' '));
      goBack();
    }, [params]);

  const animatedButtonStyle = useAnimatedStyle(() => {
    const disabled = !params.value['followers'] ? 1 : 0;
    return {
      backgroundColor: interpolateColor(
        disabled,
        [0, 1],
        [theme.primaryColor, theme.neutral3],
      ),
    };
  });

  useAutoToastError(shareError);

  return (
    <FullScreenWrapper>
      <LeftModalHeader hideSeparator={false} title={'Invite members'} />
      <MyKeyboardAvoidingView
        ref={ref}
        scrollRef={scrollRef}
        scrollInputAdditionalOffset={offset}>
        <SScrollView ref={scrollRef} keyboardShouldPersistTaps={'handled'}>
          {/*<SContainer>*/}
          {/*  <STitle>{translate('truesign.share_template_hint')}</STitle>*/}
          {/*</SContainer>*/}

          <SContainer>
            <UIUserPickerInput
              type={'user'}
              keyName={'followers'}
              multiple={true}
              title={'Invite members'}
              onChangeValue={onChangeValue}
              params={params}
            />
          </SContainer>

          <SViewHidden>
            <UIUserPickerInput
              type={'group'}
              keyName={'groups'}
              multiple={true}
              title={'Invite members groups'}
              onChangeValue={onChangeValue}
              ref={inputRef}
            />
          </SViewHidden>
          <SContainer>
            <UIButton
              title={'Select group users'}
              textStyle={styles.textStyleGroup}
              source={IC_OUTLINED_RIGHT}
              imageSize={14}
              imageStyle={styles.imageStyle}
              onPress={() => {
                inputRef?.current?.bottomSheetRef?.current?.present();
              }}
            />
          </SContainer>
          <AnimatedBottomSpace />
        </SScrollView>
      </MyKeyboardAvoidingView>

      <SBottomButton
        buttons={[
          {
            title: 'Cancel',
            onPress: goBack,
            textColor: theme.primaryColor,
            backgroundColor: 'white',
            style: {
              borderWidth: 1,
              borderColor: theme.primaryColor,
            },
            textStyle: styles.textStyle,
          },
          {
            title: 'Add',
            loading: shareLoading,
            onPress: onRequestAdd,
            textColor: 'white',
            backgroundColor: theme.primaryColor,
            textStyle: styles.textStyle,
            style: animatedButtonStyle,
          },
        ]}
      />
    </FullScreenWrapper>
  );
});

const styles = StyleSheet.create({
  textStyle: {
    fontFamily: Fonts.Medium,
    fontSize: 16,
    lineHeight: 24,
    marginRight: 8,
  },
  textStyleGroup: {color: Colors.blue1},
  imageStyle: {tintColor: Colors.blue1, justifyContent: 'center'},
});

const SScrollView = styled.ScrollView``;

const SContainer = styled.View`
  flex: 1;
  padding: 20px 16px 0 16px;
`;

const SViewHidden = styled.View`
  overflow: hidden;
  height: 0;
`;

const SBottomButton = styled(UIBottomButton)`
  padding: 8px 16px;
  margin: 0;
  background-color: ${p => p.theme.backgroundColor};
  border-top-width: 1px;
  border-color: ${p => p.theme.neutral06};
`;
